import React from "react"
import { getBriefReportSectionTitle } from "@rp/react/common/brief"
import { BriefReport, BriefReportCompanyReport } from "@rp/react/tanstack-api/brief"
import { Box } from "@rp/react/web/mui/core"
import { BriefReportSection } from "../BriefReportSection"
import { BriefReportSkeleton } from "../BriefReportSkeleton"

type BriefReportBodyProps = {
  isBriefReportRetrying: boolean
  briefReport: BriefReport | null
  onAddPrompt: (text: string, entityInfo?: BriefReportCompanyReport["entityInfo"]) => void
}
const BriefReportBody = ({
  briefReport,
  isBriefReportRetrying,
  onAddPrompt,
}: BriefReportBodyProps) => {
  if (!briefReport && !isBriefReportRetrying) return <BriefReportSkeleton />

  if (!briefReport) return null

  return (
    <Box role="article" aria-label="brief report body">
      <BriefReportSection
        title="Overview"
        body={briefReport.introduction}
        sources={briefReport.sources ?? {}}
        onAddPrompt={onAddPrompt}
      />

      {briefReport.entityReports.map(companyData => (
        <BriefReportSection
          key={companyData.entityId}
          entityInfo={companyData.entityInfo}
          title={getBriefReportSectionTitle(companyData)}
          body={companyData.cleanFinalReport}
          sources={briefReport.sources ?? {}}
          onAddPrompt={onAddPrompt}
        />
      ))}
    </Box>
  )
}

export { BriefReportBody }
